<template>
  <v-container>
    <v-layout row>
      <v-flex xs12 sm6 offset-sm3 v-if="!loading">
        <h1 class="mb3">Orders</h1>
        <v-list
          subheader
          two-line
        >
          <v-list-tile 
            avatar
            v-for="(order, i) in orders"
            :key="i"            
          >
            <v-list-tile-action>
              <v-checkbox
                color="success"
                :input-value="order.done"
                @change="markDone(order)"
              >
                
              </v-checkbox>
            </v-list-tile-action>

            <v-list-tile-content >
              <v-list-tile-title>{{ order.name }}</v-list-tile-title>
              <v-list-tile-sub-title>{{ order.phone }}</v-list-tile-sub-title>
            </v-list-tile-content>

            <v-list-tile-action>
              <v-btn
                color="cyan"
                :to="'/product/'+order.productId"
              >Open</v-btn>
            </v-list-tile-action>
          </v-list-tile>

        
        </v-list>
      </v-flex>
      <v-flex xs12 class="text-xs-center pt-5" v-else>
        <v-progress-circular
            :size="100"
            :width="4"
            color="cyan"
            indeterminate
        ></v-progress-circular> 
      </v-flex>     
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data () {
    return {
      orders: [
        {
          id: 'test',
          name: 'Konstantin',
          productId: '1',
          done: false
        }
      ]
    }
  },
  computed: {
    loading () {
      return this.$store.getters.loading
    }
  },
  methods: {
    markDone (order) {
      order.done = true
    }
  }
}
</script>


